.card {
  position: relative;
}

.card img {
  width: 80%;
  display: block;
  border: 2px solid #fff;
  border-radius: 6px;
}

/*front of card - the picture */
.card .front {
  transform: rotateY(90deg);
  transition: all ease-in 0.2s;
  // absolute position puts it sitting on top of the card cover
  position: absolute; 
}
.flipped .front {
  transform: rotateY(0deg);
  transition-delay: 0.2s;
}

/*back of card - the cover */
.card .back {
  transition: all ease-in 0.2s;
  transition-delay: 0.2s;
}

.flipped .back {
  transform: rotateY(90deg);
  transition-delay: 0s;
}